import React from "react";

//MaterialUI
import Box from "@mui/material/Box";
import { makeStyles } from "@material-ui/core/styles";
import { deletePreferencia } from "../../services/preferenciasUser";
//styles
import "../../styles/modalsProfile.css";

/**
 * Componente para mostrar las preferencias del usuario y el boton de agregar mas preferencias.
 * @param {object} prefUsers
 * @param {Function} handleShow
 * @returns Componente con preferencias y boton de agregar.
 */
const Preferencias = ({ prefUsers, handleShow, ...props }) => {
  const handleEliminarPreferencia = async (id) => {
    console.log(id);
    console.log(prefUsers);
    const response = await deletePreferencia(id)
    console.log(response);
     /* return await deletePreferencia(id); */

  }
  const useStyles = makeStyles((theme) => ({
   
    title1: {
      textAlign: "center",
    },
    "@media only screen and (max-width: 1000px)": {
      imagenPerfil: {
        width: "100%",
      },
    },
  }));

  const classes = useStyles();

  return (
    <div className="container container1">
      <h3 className={classes.title1}>Preferencias</h3>
      
      {!prefUsers ? (
        <Box display="flex" mt={5} justifyContent="space-between">
          <div className="d-grid  col-8  mx-auto">
            <button
              className="btn btn-secondary btn-lg"
              type="submit"
              onClick={handleShow}
            >
              Ingresar Preferencia
            </button>
          </div>
        </Box>
      ) : (
        <>
          <div className="container ">
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                flexWrap: "wrap",
              }}
              className="btn-group btn-group-lg"
            >
              <div className="d-flex flex-wrap">
              {prefUsers &&
                prefUsers.map((categ, index) => (
                  <span
                    sx={{ color: "#fff" }}
                    key={index}
                    className={"bg-white px-3 py-2 rounded m-2 border-0 text-dark d-flex justify-content-between align-items-center " + classes.botonList}
                    >
                      
                    <h6 className="pt-2 pe-4">{categ.categoria}</h6> <button className="btn btn-danger" onClick={ ()=>{handleEliminarPreferencia(categ.id)}}>✕</button>
                  </span>
                ))}
                </div>
            </Box>
          </div>
          <br></br>
          <div className="container">
            <div className="d-grid  col-8  mx-auto">
              <button
                className="btn btn-secondary btn-lg"
                type="submit"
                onClick={handleShow}
              >
                Ingresar Preferencia
              </button>
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default Preferencias;
